.lieu-creneaux-card {
  max-height: 70vh;
  overflow-y: auto;
}

.creneaux-row .col {
  margin-left: 2px;
  margin-right: 2px;

  &:first-child {
    margin-left: none;
  }
  &:last-child {
    margin-right: none;
  }
}

.creneau {
  div {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  small {
    white-space: nowrap;
  }
}

.collapsed .creneau:nth-child(6){
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
    background: linear-gradient(to top, rgb(255, 255, 255), rgba(226, 234, 238, 0.5));
  }
}

